* {
    box-sizing: border-box;
}

html{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif, Helvetica, Arial, sans-serif;
    color: $text;
    // background: linear-gradient(304deg, rgba(34,34,53,1) 38%, rgba(39,39,81,1) 100%);

    background: linear-gradient(324deg, rgba(11,11,13,1) 14%, rgb(44, 44, 44) 48%);
}

body {
    margin: 0;
}

h1,h2,h3,h4,h5,h6 {
    margin: 0;
    font-weight: 400;
}

a{
    all: unset;
    color: inherit;


    &:focus{
        outline: none;
    }
}

button{
    all: unset
}


