
.btn{
    font-weight: 600;
    background-color: $pop;
    color: $nutral;
    font-size: 0.95em;
    text-align: center;
    padding: 5px 10px;
    // margin: 5px;
    
    &:hover{
        background-color: lighten($pop,5%);
        cursor: pointer;
    }
    &:active{
        box-shadow:inset 0 0 8px darken($pop,5%);
    }
}

.nav-btn {
    box-shadow:0 ;
    transition: all .6s;
    &:hover {
        background-color: rgba($pop,0.05);
        box-shadow: inset 0 -3px 0 rgba($pop ,0.4);
        cursor: pointer;
    }
}

@media (max-width: 600px) {
    .nav-btn { 
        &:hover {
            box-shadow: inset 3px 0 0 rgba($pop ,0.4);
        }
    }
}

